<template>
	<view class="">
		<view class="commit">
			<select class="select">
				<option v-for="(item,index) in taglist" :key="index"  @click="getIndex(index)" >
					 {{item.tagName}}{{index}}
				 </option>
			</select>
			<text @click="commit">提交</text>
		</view>
		<view class="box">
			<textarea class="context" v-model="message" value="" placeholder="请输入想要发布的帖子" />
		</view>
		<view class="upload">
			<uni-file-picker 
			    v-model="imageValue" 
			    fileMediatype="image" 
			    mode="grid" 
			    @select="select" 
			    @progress="progress" 
			    @success="success" 
			    @fail="fail" 
			/>
 
		</view>
 
 
</view>
</template>

<script>
import axios from 'axios'
export default {
        data() {
					return {
						num:'',
							userId:'',
							message:'',
							imageValue:[],
							taglist:[]
            }
        },
			created() {
					this.getlist()
				},
        methods:{
					getlist(){
						axios
							.get('http://localhost:8001/tag/allTag',{
							
						}).then((response) => {
							 this.taglist = response.data.data.TagList
							 console.log(this.taglist + "$$$")
						}).catch(function(error) {
							console.log(error);
							uni.showToast({
								title: '请求tag列表失败',
								duration: 2000
							});
						});
					},
					commit(){
						this.userId =  window.sessionStorage.getItem('userId')
						axios
							.get('http://localhost:8001/comment/addComment', {
								params:{
									userId: this.userId,
									comments: this.message,
									tagId: 1
								},
		 
							}).then((response) => {
								 
							}).catch(function(error) {
								console.log(error);
								uni.showToast({
									title: '请求评论失败',
									duration: 2000
								});
							});
					},
					getIndex(index){
						console.log(index)
					},
            // 获取上传状态
            select(e){
                console.log('选择文件：',e)
            },
            // 获取上传进度
            progress(e){
                console.log('上传进度：',e)
            },

            // 上传成功
            success(e){
                console.log('上传成功')
            },

            // 上传失败
            fail(e){
                console.log('上传失败：',e)
            },
 
 
        },
 
}
</script>

<style scoped lang="scss">
	.commit {
		position: relative;
		padding-left: 660rpx;
		margin: 20rpx 0px;
		color: red;
	}
	.select {
		position: absolute;
		top: 4px;
		left: 15px;
	}
	.box {
		height: 300rpx;
		margin: 10rpx 20rpx;
		background-color: rgb(249, 249, 249);
		border-radius: 10rpx;
	}
	.context {
		padding: 10rpx 20rpx;
	}
	.upload {
		padding: 40rpx 20rpx;
		border-bottom: solid 1px #eee;
	}
	.getDate {
		display: flex;
		padding: 50rpx 20rpx;
		border-bottom: solid 1px #eee;
		.date {
			padding-left: 10rpx;
			.uni-input {
				color: rgb(150, 150, 150)
			}
		}
		.time {
			padding-left: 20rpx;
			.uni-input {
				color: rgb(150, 150, 150)
			}
		}
	}
</style>
